.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 50px 0;
}

.app > * {
  width: 100%;
  padding: 0 20px;
  margin: 0 0 10px 0;
  max-width: 400px;
  min-width: 320px;
}

.app > :nth-child(2) {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 380px) {
  .app > :nth-child(1),
  .app > :nth-child(2) {
    flex-wrap: wrap;
    justify-content: center;
  }

  .app > :nth-child(2) > :first-child {
    margin: 0 0 10px 0;
  }

  .app > :nth-child(2) > :last-child button {
    padding: 10px 15px;
  }
}